@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 20px 60px;
}

a {
  text-decoration: none;
  color: #161e20;
}

* {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
  font-family: "Quicksand";
}

.App {
  margin: 0 5rem;
}

.home-container{display:flex;flex-direction:row;flex-wrap:wrap;padding:1rem 1rem;justify-content:center;color:#8d6c9f}.home-container h2{text-align:center}.home-container h3{margin-bottom:0;text-align:center}.home-container .title-box{display:flex;flex-direction:column;flex-wrap:wrap;position:relative}.home-container .form{padding:1rem}.home-container .title{text-align:left}.home-container .add-contact{border:2px solid #85cbf8;max-width:100px;color:#888888;font-size:0.8rem;display:flex;padding:0.5rem 0.8rem;border-radius:4px;max-height:38px;cursor:pointer;background-color:white;z-index:9}.home-container .add-contact p{margin:auto}.home-container .add-contact img{margin-right:5px}.home-container .add-contact:hover{background-color:#85cbf8;font-weight:700;transition:0.2s ease-in-out}.home-container .directory{min-width:70%;margin-top:2rem;display:flex;flex-wrap:wrap}.full-row{display:flex;justify-content:space-between;width:100%;margin:0 3rem}.csv-box{border:2px solid #72caaf;max-width:100px;color:#888888 !important;font-size:0.8rem;display:flex;padding:0.5rem 0.8rem;border-radius:4px;max-height:38px;position:relative;margin-left:1rem;cursor:pointer;background-color:white;z-index:9}.csv-box p{margin:auto}.csv-box img{margin-right:5px}.csv-box:hover{background-color:#72caaf;font-weight:700;transition:0.2s ease-in-out}

.group{position:relative;margin:25px 0}.group .form-input{background:none;background-color:#e8f0fe;color:#828282;font-size:14px;padding:5px 5px 5px 5px;display:block;width:100%;border:none;border-radius:4px;border:1px solid #72ca}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:rgba(119,34,204,0.666667)}.group input[type="password"]{letter-spacing:0.3em}.group .form-input-label{color:#828282;font-size:13px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:rgba(119,34,204,0.666667)}

.add-contact-card{display:flex;flex-direction:column;border:2px solid #72caaf;justify-content:center;padding:2rem;border-radius:6px;min-height:380px;position:absolute;top:200px;width:280px;right:60px;background-color:white;z-index:1000}.success-card{display:flex;flex-direction:column;justify-content:center;padding:2rem;background-color:#72caaf;min-height:380px}.gender-select{display:flex;color:#828282;font-size:13px;margin-left:5px}.gender{margin-left:8px;color:black;font-size:11px}.svg-cont{display:flex;justify-content:center;text-align:center}

.contact-card{padding:1rem;background-color:#faefde;color:#8d6c9f;display:flex;margin:1rem;border:2px solid #8d6c9f;border-radius:4px;min-width:300px;justify-content:space-around;position:relative}.contact-logo{display:flex;flex-direction:column;justify-content:center}.contact-logo img{width:auto}.data{margin-top:1rem}.data-row{margin-bottom:0.5rem;font-size:0.7rem}.data-row span:first-child{font-weight:600}.icon-row{display:flex;justify-content:flex-end;position:absolute;z-index:99;top:8px;right:8px}.icon-row img{margin-left:5px;cursor:pointer}.icon-row img:hover{transition:0.2s ease-in-out;transform:scale(1.1)}

.search-box{display:flex;width:70%;justify-content:flex-end}.search-bar{border-radius:4px;border:2px solid #8d6c9f;margin-right:8px;font-style:italic;min-width:50%;padding:16px}.dropdown{border-radius:4px;border:2px solid #8d6c9f;background-color:#c2cde7;color:#8d6c9f;font-weight:700;cursor:pointer}

.auth{width:850px;display:flex;justify-content:space-between;margin:80px auto}

.sign-in{min-width:300px;display:flex;flex-direction:column;color:#8d6c9f;padding:5rem 3rem;border:2px solid #72ca;border-radius:8px;margin:3rem;margin-left:0;background-color:#fff9f0}.sign-in .title{margin:10px 0}.sign-in .button-group{display:flex;flex-wrap:wrap;justify-content:center;width:100%;margin-top:4rem}

.custom-button{min-width:160px;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:12px;background-color:#85cbf8;color:#8d6c9f;text-transform:uppercase;border-radius:8px;border:2px solid #72caaf;font-weight:700;cursor:pointer;text-align:center}.custom-button:hover{background-color:#72caaf;border:2px solid #85cbf8}

.sign-up{min-width:350px;display:flex;color:#8d6c9f;flex-direction:column;padding:5rem 3rem;border:2px solid #72ca;border-radius:8px;margin:3rem;background-color:#fff9f0}.sign-up .title{margin:10px 0}

.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px;color:#8d6c9f;font-weight:600}.header .logo-container{height:100%;width:70px;padding:15px}.header .logo{width:5vh;height:auto}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;font-size:1.2rem;color:#8d6c9f}.header .options .option-link:hover{border-bottom:2px solid #8d6c9f;cursor:pointer}

